.jumbotron {
  padding: 20px;
  text-align: center;
  height: 500px;
  color: #4d2427;
}

.jumbotron h1 {
  color: #3b1d3a;
}

.container-jumbotron {
  border: 2px solid #9b7e96;
  border-radius: 15px; /* Border radius for rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Box shadow for a subtle shadow effect */
  padding: 100px; /* Padding inside the jumbotron */
  background-color: #fff; /* Background color for the jumbotron */
  margin: 50px;
}

.pretty-button {
  background-color: #4b1246;
  padding: 10px 250px;
  font-size: 18px;
  color: #f3aeed;
  margin: 60px 0 0 0;
  display: inline-block;
  text-decoration: none;
  float: none;
  border-radius: 15px;
  margin-bottom: 30px;
}
